export const mutations = {
  cartoonboxs(state, result) {

    var list = result.data.list
    // console.log('list  ==> ', list );

    var listL = list.length
    // console.log("listL ==>> ", listL )
    let start = Math.floor(  Math.random() * (listL - 6))
    // console.log("start ==>> ", start )

    state.homeData.data.list.push(...result.data.list)
    state.cartoonboxD.data = list.slice( start , start + state.cartoonboxD.count);

    // console.log('state.cartoonboxD.data  ==> ', state.cartoonboxD.data );

  },

  fictionboxs(state, result) {

    var xslist = result.data.list
    // console.log('xslist  ==> ', xslist );

    var xslistL = xslist.length
    // console.log("xslistL ==>> ", xslistL )
    let start = Math.floor( Math.random() * (xslistL - 3) )
    // console.log("start xs ==>> ", start )

    state.homeData.data.xslist.push(...result.data.list)
    state.fictionboxD.data = xslist.slice( start , start + state.fictionboxD.count);

    // console.log('state.fictionboxD.data  ==> ', state.fictionboxD.data );

  }
}
