import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import axios from 'axios'

import VueAxios from 'vue-axios'

import {
  Field,
  CellGroup,
  Button,
  Toast,
  Uploader,
  Tabbar,
  TabbarItem,
  Icon,
  Tab,
  Tabs,
  NavBar,
  Swipe,
  SwipeItem,
  Cell,
  Image ,
  Pagination,
  Popup,
  Lazyload,
  Search 
} from 'vant'

import 'lib-flexible/flexible'

Vue.use(VueAxios,axios)

Vue
  .use(Field)
  .use(CellGroup)
  .use(Button)
  .use(Toast)
  .use(Uploader)
  .use(Tabbar)
  .use(TabbarItem)
  .use(Icon)
  .use(Tab)
  .use(Tabs)
  .use(NavBar)
  .use(Swipe) 
  .use(SwipeItem) 
  .use(Cell) 
  .use(Image)  
  .use(Pagination) 
  .use(Popup) 
  .use(Lazyload) 
  .use(Search)
  
Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
