export const mutations = {

  getdeteilsData(state, data) {
    state.detailsData = data;
    // console.log(' detailsData ==> ',  state.detailsData)
    // console.log(' detailsData.cover ==> ', state.detailsData.data.cover)
  },

  changeDisplay(state) {
    state.display = state.display == 'block' ? '-webkit-box' : 'block';
  },

  detailsSet(state, result) {
    
    state.details.list = result.data.list
    // console.log('list  ==> ', state.details.list);

    var listL = state.details.list.length
    // console.log("listL ==>> ", listL)

    // state.details.listT.push(...result.data.list)
    state.details.listT = state.details.list.slice(state.params.startIndex, state.params.count)

    // state.detailsData = result.data.data;
    // console.log(' detailsData ==> ', state.detailsData)


  },

  resetState(state) {
    state.detailsData = {
      data: {
        cover: {}
      }
    }
    state.details = {
      list:[]
    }
  },
}
