import Vue from 'vue'
import Vuex from 'vuex'

import {loginModule} from './loginStore/loginModule'

import {registerModule} from './registerStore/registerModule'

import {indexModule} from './indexStore/indexModule'

import {homeModule} from './homeStore/homeModule'

import {videosModule} from './videosStore/videosModule'

import {bookshelfModule} from './bookshelfStore/bookshelfModule'

import {cartoonsModule} from './cartoonStore/cartoonsModule'

import {fictionModule} from './fictionStore/fictionModule'

import {particularsModule} from './particularsStore/particularsModule'

import {detailsModule} from './detailsStore/detailsModule'

import {contentModule} from './contentStore/contentModule'

import {matterModule} from './matterStore/matterModule'

Vue.use(Vuex)

export default new Vuex.Store({
  
    modules:{
        // 登录,注册模块
        loginModule ,registerModule,

        // 页面
        indexModule,
        // 首页
        homeModule,
        // 小说
        fictionModule,
        // 漫画
        videosModule, cartoonsModule,
        // 书架
        bookshelfModule,

        // 详情
        particularsModule,detailsModule,

        // 内容
        contentModule,matterModule
    }
})
