export const mutations = {
  changecartoons(state, result) {

      if (state.isInit) {
        state.isInit = false;
      }
  
  
      var xslist = result.data.list
      // console.log('xslist ==> ', xslist);
  
      var xslistL = xslist.length
      // console.log('xslistL ==> ', xslistL);
  
      // state.fictionData.data.list.push(...result.data.list)
  
      state.fictionData.data.list = xslist.slice(0, state.params.startIndex + state.params.count);
  
      if (state.params.startIndex > xslist) {
        // console.log('state.params.startIndex s ==> ', state.params.startIndex);
        // console.log('xslist s ==> ', xslist);
        state.isHas = false;
        // console.log('state.isHas  ==> ',state.isHas );
      }
      state.params.startIndex += state.params.count

      // 重置下次开始截取数据的下标
      // state.params.startIndex += state.params.count;
      // console.log("state.params.startIndex > mhlistL", state.params.startIndex >  mhlistL)
      // console.log('state.params.count ==> ', state.params.count);
      // console.log('state.params.startIndex ==> ', state.params.startIndex);
      // console.log('result.data.mhlist 1 ==> ', result.data.mhlist);
      // console.log('state.cartoonsData ==> ', state.cartoonsData);
      // console.log('state.cartoonsData.data.mhlist 11 ==> ', state.cartoonsData.data.mhlist);
      // console.log('result.data.mhlist.length ==> ', result.data.mhlist.length);
    }
}
