export const mutations = {

  getparticularsData(state, data) {
    state.particularsData = data;
    // console.log(' particularsData ==> ', state.particularsData)
    // console.log(' particularsData.cover ==> ', state.particularsData.data.cover)
    //  console.log(' particularsData dd ==> ', state.particularsData.list[0].url)
  },

  changeDisplay(state) {
    state.display = state.display == 'block' ? '-webkit-box' : 'block';
  },

  paginationSet(state, result) {
    state.pagination.list = result.data.list
    // console.log('list  ==> ', state.pagination.list);

    var listL = state.pagination.list.length
    // console.log("listL ==>> ", listL)

    // // 总记录数
    state.pagination.totalItems = listL

    // 每页个数
    let temsL = state.pagination.tems = 10
    //  console.log("temsL ==>> ", temsL )

    // 共几页
    state.pagination.count = Math.ceil(listL / temsL)

    // console.log("count ==>> ", state.pagination.count)

    // console.log('currentPage ==>> ', state.pagination.currentPage)

  },

  resetState(state) {
    state.particularsData = {
      data: {
        cover: {}
      }
    }
    state.pagination = {
      list: []
    }
  },

  mhurl1(state, mhurl1) {
    state.gotoUrl.mhurl1 = mhurl1
    // console.log('state.gotoUrl.mhurl1 ==> ', state.gotoUrl.mhurl1)
  },
}
