export const mutations = {

  // 查看密码
  checkPassword(state, key) {
    // console.log('key ==>> ', key);
    // console.log(state[key].value);

    let isOpen = state[key].type === 'password';

    state[key].type = isOpen ? 'text' : 'password';

    state[key].icon = isOpen ? 'eye-o' : 'closed-eye';
  },


  // 点击登录
  login(state, pass) {
    state.isLoading = pass
  },

  // 验证用户名
  validUsername(state, isValid) {
    // console.log('isValidU ==>> ', isValid);
    state.user.errorMessage = isValid ? '' : '用户名支持4-8个(字母数字_)组合';

    let users = localStorage.getItem("users");

    users = users ? JSON.parse(users) : [];

    // 查找用户
    for (let i = 0; i < users.length; i++) {
      if (users[i].username == this.user.value) {
        this.pho.src = user.pho[0].content;
      }
    }
  },

  // 验证密码
  vaildPassword(state, isValid) {
    // console.log('isValidP ==>> ', isValid);
    state.pwd1.errorMessage = isValid ? '' : '密码包含数字、字母或特殊字符'
  },

  passForm(state) {
    state.isValid = (state.user.errorMessage == '' && state.user.value != '') && (state.pwd1.errorMessage == '' && state.pwd1.value != '')
  },
}
