import routesComponent from './routesComponent'
export default [{
    path: '/loginform',
    name: 'loginform',
    component: routesComponent.loginformComponent,
    children: [{
        // 注册
        path: '/register',
        name: 'register',
        component: routesComponent.registerComponent
      },
      {
        // 登录
        path: '/login',
        name: 'login',
        component: routesComponent.loginComponent
      },
    ]
  },
  {
    path: '/index',
    name: 'index',
    component: routesComponent.indexComponent,
    children: [{
        path: '/home',
        name: 'home',
        component: routesComponent.homeComponent,
      },
      {
        path: '/fiction',
        name: 'fiction',
        component: routesComponent.fictionComponent,
      },
      {
        path: '/bookshelf',
        name: 'bookshelf',
        component: routesComponent.bookshelfComponent,
      },
      {
        path: '/videos',
        name: 'videos',
        component: routesComponent.videosComponent,
      },
      {
        path: '/mine',
        name: 'mine',
        component: routesComponent.mineComponent,
      },
    ],
  },
  {
    path: '/particulars/:url',
    name: 'particulars',
    component: routesComponent.particularsComponent,
  },
  {
    path: '/details/:url',
    name: 'details',
    component: routesComponent.detailsComponent,
  },
  {
    path: '/matter/:url',
    name: 'matter',
    component: routesComponent.matterComponent,
  },
  {
    path: '/content',
    name: 'contents',
    component: routesComponent.contentComponent,
  },
  {
    path: '/search',
    name: 'search',
    component: routesComponent.searchComponent,
  },
  {
    path: '/cartoons',
    name: 'cartoons',
    component: routesComponent.cartoonsComponent
  },

  {
    path: '*',
    redirect: {
      name: 'home'
    }
  }
]
