export const mutations = {
  changecartoons(state, result) {

    if (state.isInit) {
      state.isInit = false;
    }

    if (state.isInitial) {
      state.isInitial = false;
    }

    var mhlist = result.data.list.slice(1,-1)
    // console.log('mhlist ==> ', mhlist);

    var mhlistL = mhlist.length
    // console.log('mhlistL ==> ', mhlistL);

    // state.cartoonsData.data.list.push(...result.data.list)

    state.cartoonsData.data.list = [...mhlist].splice(0, state.params.startIndex+state.params.count);

    if (result.data.list.length < state.params.count) {
      // console.log("result.data.list.length ", result.data.list.length)
      state.isHas = false;
    }

    state.params.startIndex += state.params.count;
    // console.log('state.params.startIndex ==>> ', state.params.startIndex)

  },

  
  resetState(state) {
    state.cartoonsData = {
      data:{
        list:{}
      }
    }
  },

  changevidosBox(state, o) {
    state.vidosBoxData.vidosBox = o.vidosBox;
    // console.log("state.vidosBoxData.vidosBox ==> ", state.vidosBoxData.vidosBox)

    state.vidosBoxData.vidosBoxH = o.vidosBoxH
    // console.log("state.vidosBoxData.vidosBoxH ==> ", state.vidosBoxData.vidosBoxH)
  }
}
